import { ref } from 'vue';
import { getCurrentInstance } from "vue"; // import {
// 	reactive
// } from 'vue';

export default {
  setup() {
    const result = ref([]);
    const sum = ref(0);
    const bool = false;
    const btnall = ref([]);
    const checked = ref([])[0] == undefined ? false : ref([]);
    const checkboxGroup = ref(null);
    let {
      proxy
    } = getCurrentInstance();

    const img = require("@/assets/without.png");

    const count = () => {
      // console.log(proxy.$store.state.data)
      // console.log(proxy.btnall)
      // console.log(proxy.checked[0])
      let tt = proxy.result.map(item => {
        return item;
      });
      let sum = 0;
      tt.forEach(item => {
        console.log(proxy.$store.state.data[item].num);
        sum += Number(proxy.$store.state.data[item].num) * Number(proxy.$store.state.data[item].money);
      });
      proxy.sum = sum * 100;
    };

    const checkAll = () => {
      proxy.result = !proxy.result;
      proxy.$refs.checkboxGroup.toggleAll({
        result: proxy.result,
        skipDisabled: true
      });
      count();
    };

    const all = () => {
      if (proxy.result.length == proxy.$store.state.data.length) {
        proxy.$refs.checkboxGroup.toggleAll(false);
        proxy.btnall = "";
      } else {
        proxy.$refs.checkboxGroup.toggleAll(true);
        proxy.btnall = ref([]);
      }

      count();
    };

    return {
      all,
      sum,
      img,
      bool,
      count,
      btnall,
      result,
      checked,
      checkAll,
      checkboxGroup
    };
  },

  watch: {
    //判断全选
    'result'(val) {
      let count = 0;

      for (let i = 0; i < this.$store.state.data.length; i++) {
        if (this.$refs.checkbox[i].disabled === true && this.$refs.checkbox[i].checked === false) {
          count++;
        }
      }

      if (val.length !== this.$store.state.data.length - count) {
        this.checked = false;
        this.btnall = false;
      } else {
        this.checked = true;
        this.btnall = true;
      }
    }

  },

  beforeCreate() {
    let sum = 0;
    let arr = this.$store.state.data.map(val => {
      let m = Number(val.num) * Number(val.money);
      return m;
    });
    arr.forEach(item => {
      sum += item;
    });
    this.sum = sum * 100;
  },

  created() {
    this.btnall = false;
    this.sum = 0; // console.log(this.$store.state.data)
  }

};